.btn-mint{
    border: 1px solid green;
	-webkit-backdrop-filter: blur(10px);
	        backdrop-filter: blur(10px);
	-webkit-transform: skewX(-10deg);
	        transform: skewX(-10deg);
	height: 40px;
	width: 180px;
	border-radius: 20px 5px 20px 0px;
	position: relative;
	display: flex;
	align-items: center;
	justify-content: center;
	transition: all .3s ease;
	font: 15px sans-serif;
	font-weight: 300;
	text-shadow: 0 0 20px #fff;
	text-transform: uppercase;
	-webkit-animation: breath2 2s .5s infinite alternate;
	        animation: breath2 2s .5s infinite alternate;
	transition: all .2s ease;
	cursor: pointer;
    background-image: linear-gradient(to bottom, #2c3e50, rgba(5, 35, 99, 0.87));
    border-color: rgba(168, 236, 255, 1);
		background-image: linear-gradient(to bottom, transparentize(gba(48, 138, 255, 1), .5), transparentize(rgba(29, 96, 240, 1), .5));
		box-shadow: 0 0 70px transparentize(gba(48, 138, 255, 1), .5), 0 5px 20px transparentize(gba(48, 138, 255, 1), .5), inset 0 1px rgba(255, 236, 168, 1), inset 0 -1px rgba(255, 236, 168, 1);
		color: rgba(168, 236, 255, 1)
}

.btn-mint:before{
    content: "";
    display: block;
    width: calc(100% - 22px);
    height: calc(40px - 8px);
    -webkit-animation: breath 2s infinite alternate;
            animation: breath 2s infinite alternate;
    left: 10px;
    top: 3px;
    position: absolute;
    background-color: transparent;
    border: 1px solid #fff;
    border-radius: 15px 3px 15px 3px;
    box-shadow: inset 0 0 30px 0 rgba(168, 236, 255, 1)

}

.btn-mint::hover{
    box-shadow: 0 0 70px rgb(48 138 255 / 80%), 0 5px 20px rgb(48 138 255 / 80%), inset 0 1px #a8ecff, inset 0 -1px #a8ecff;
}


.btn-mint .blue{
    border-color: rgba(168, 236, 255, 1);
		background-image: linear-gradient(to bottom, transparentize(gba(48, 138, 255, 1), .5), transparentize(rgba(29, 96, 240, 1), .5));
		box-shadow: 0 0 70px transparentize(gba(48, 138, 255, 1), .5), 0 5px 20px transparentize(gba(48, 138, 255, 1), .5), inset 0 1px rgba(255, 236, 168, 1), inset 0 -1px rgba(255, 236, 168, 1);
		color: rgba(168, 236, 255, 1)
		
}

@-webkit-keyframes breath2 {
    0% {
        -webkit-transform: skewX(-10deg) scaleX(1);
                transform: skewX(-10deg) scaleX(1);
    }
    100% {
        -webkit-transform: skewX(-10deg) scaleX(0.95);
                transform: skewX(-10deg) scaleX(0.95);
    }
}

@keyframes breath2 {
    0% {
        -webkit-transform: skewX(-10deg) scaleX(1);
                transform: skewX(-10deg) scaleX(1);
    }
    100% {
        -webkit-transform: skewX(-10deg) scaleX(0.95);
                transform: skewX(-10deg) scaleX(0.95);
    }
}






